@font-face {
    font-family: 'Gotham Book';
    src: url('./Gotham-Book.eot');
    src: url('./Gotham-Book.eot?#iefix') format('embedded-opentype'),
        url('./Gotham-Book.woff2') format('woff2'),
        url('./Gotham-Book.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

